import { link, Theme } from "../refs/refs";

if (localStorage.getItem("theme") === "dark-theme") {
	link.switchTheme.checked = true;
	link.body.classList.add(localStorage.getItem("theme"));
}

const switcher = e => {
	e.preventDefault();

	if (link.switchTheme.checked === true) {
		link.body.classList.remove(localStorage.getItem("theme"));
		localStorage.setItem("theme", Theme.DARK);
		link.body.classList.add(localStorage.getItem("theme"));
	} else if (link.switchTheme.checked === false) {
		link.body.classList.remove(localStorage.getItem("theme"));
		localStorage.setItem("theme", Theme.LIGHT);
		link.body.classList.add(localStorage.getItem("theme"));
	}
};

link.switchTheme.addEventListener("change", switcher);
